export class Task{
  constructor(
    public id?: number,
    public title?: string,
    public description?: string,
    public priority?: number,
    public due_date?: string,
    public completed?: boolean,
    public updated_at?: string,
		private _marked: boolean = true
  ) {}

  get marked(): boolean {
    return this._marked;
  }

   set marked(m: boolean) {
    this._marked = m;
  }
}



